import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withStyles, Grid, Typography, Card, Button, TextField } from '@material-ui/core';
import ReactApexChart from "react-apexcharts";
import { getDasboardData, getPendingOrders, getTopSellingProducts } from '../../../store/actions/dashboard_action';
import Skeleton from '@material-ui/lab/Skeleton';
import money from '../../../assets/images/svg/money.svg';
import delivery from '../../../assets/images/svg/delivery-box.svg';
import lighting from '../../../assets/images/svg/lighting.svg';
import users from '../../../assets/images/svg/users.svg';


/**Created common styles for UI.*/
const styles = theme => ({
	root: {
		textAlign: 'center',
		padding: theme.spacing(4)
	},
	carda: {
		backgroundColor: "	#FEC9B4",
		padding: "15px 15px",
		borderRadius: 8,
		boxShadow: "0px 0px 0px 0px #E1E7ED",
	},
	cardb: {
		backgroundColor: "#57B78C",
		padding: "15px 15px",
		borderRadius: 8,
		boxShadow: "0px 0px 0px 0px #E1E7ED",
	},
	cardc: {
		backgroundColor: "#5EC8E6",
		padding: "15px 15px",
		borderRadius: 8,
		boxShadow: "0px 0px 0px 0px #E1E7ED",
	},
	cardd: {
		backgroundColor: "#F4CC44",
		padding: "15px 15px",
		borderRadius: 8,
		boxShadow: "0px 0px 0px 0px #E1E7ED",
	},
	row: {
		display: "flex"
	},
	cardBlackTextTitle: {
		fontSize: 15,
		color: "#000",
		paddingLeft: 10,
		fontFamily: "SourceSansPro-Regular",
	},
	cardBlackSecondText: {
		fontSize: 18,
		paddingTop: 15,
		fontWeight: "bold",
		color: "#000",
		fontFamily: "SourceSansPro-Regular",
	},
	cardBlackThirdText: {
		fontSize: 13,
		fontWeight: "bold",
		paddingTop: 15,
		paddingLeft: 5,
		color: "#000",
		fontFamily: "SourceSansPro-Regular",
	},
	cardWhiteTextTitle: {
		fontSize: 15,
		color: "#fff",
		paddingLeft: 10,
		fontFamily: "SourceSansPro-Regular",
	},
	cardWhiteSecondText: {
		fontSize: 18,
		paddingTop: 15,
		fontWeight: "bold",
		color: "#fff",
		fontFamily: "SourceSansPro-Regular",
	},
	cardWhiteThirdText: {
		fontSize: 13,
		fontWeight: "bold",
		paddingTop: 15,
		paddingLeft: 5,
		color: "#fff",
		fontFamily: "SourceSansPro-Regular",
	},
	textBadge: {
		borderRadius: 15,
		padding: "3px 10px",
		backgroundColor: "#F3852A",
		color: "#fff",
		fontSize: 12,
		fontWeight: "600",
		fontFamily: "SourceSansPro-Regular",
	},
	rowContainer: {
		display: "flex",
		justifyContent: "space-between",
		alignItems: "center",
		padding: 10,
		borderTop: "1px solid #cccccc"
	},
	cardTitle: {
		padding: "10px",
		textAlign: "left",
		fontSize: 18,
		color: "#000",
		fontFamily: "SourceSansPro-SemiBold",
	},
	whiteCard: {
		width: "100%",
		backgroundColor: "#fff",
		borderRadius: 5,
		boxShadow: "0px 0px 0px 0px #E1E7ED",
	},
	normalText: {
		fontSize: 15,
		textAlign: "left",
		color: "#000",
		fontFamily: "SourceSansPro-Regular",
	},
	button: {
		margin: "10px 10px",
		padding: "3px 20px",
		border: "1px solid #666666",
		borderRadius: 6,
		fontFamily: "SourceSansPro-Regular",
		textTransform: "none"
	},
	inputDrawerRoot: {
		backgroundColor: "#FFFFFF",
		border: "1px solid #E1E7ED",
		borderRadius: "4px",
		fontSize: "13x !important",
		fontFamily: "SourceSansPro-Regular",
		"&:hover": {
			border: "1px solid #57B78C",
			backgroundColor: "#FFFFFF"
		}
	},
	inputDrawerStyle: {
		fontFamily: "SourceSansPro-Regular !important",
		fontSize: "13x !important",
		padding: "10px !important",
		paddingRight: "32px !important",
		color: "#6E8CA8",
		opacity: 1,
		"&&:after": {
			color: "#57B78C",
		}
	},
	underline: {
		"&&&:before": {
			borderBottom: "none"
		},
		"&&:after": {
			borderBottom: "none"
		}
	},
});

/**
 * @class
 * Class representing Dashboard component
 */
class Dashboard extends Component {
	constructor(props) {
		super(props);
		this.state = {
			val: 0
		};
	}

	/** This is a component life cycle method and used to fetch datas*/
	componentDidMount = () => {
		/** fetching dashboard data. */
		this.props.dispatch(getDasboardData());
		/** fetching top selling products. */
		this.props.dispatch(getTopSellingProducts());
		/** fetching pending orders. */
		this.props.dispatch(getPendingOrders())
	}

	/**
	* @function
	* this function is used to view sales area chart.
	*/
	returnSalesChart = () => {
		let series = [{
			name: 'This Week',
			type: 'area',
			data: [4400, 5500, 3100, 4700, 3100, 4000, 3900]
		}, {
			name: 'Last Week',
			type: 'line',
			data: [3300, 6900, 3000, 6100, 3300, 3800, 3100]
		}];
		let chartOptions = {
			chart: {
				height: 250,
				type: 'line',
				fontFamily: "SourceSansPro-Regular",
				stacked: false,
				toolbar: {
					show: false,
				},
				zoom: {
					enabled: false,
				}
			},
			title: {
				text: "Average Sales (in S$)",
				align: 'left',
				floating: true,
			},
			stroke: {
				curve: 'smooth',
				width: [2, 1],
				dashArray: [0, 8]
			},
			fill: {
				colors: ['#FEEFE8', '#FEEFE8'],
				opacity: 0.9,
				type: 'solid',
				gradient: {
					shade: 'dark',
					type: "horizontal",
					shadeIntensity: 0.5,
					gradientToColors: undefined,
					inverseColors: true,
					opacityFrom: 1,
					opacityTo: 1,
					stops: [0, 50, 100],
					colorStops: []
				},
			},
			colors: ["#E4B5A2", "#cccccc"],
			labels: ["23/6", "24/6", "25/6", "26/6", "27/6", "28/6", "29/6"],
			markers: {
				size: 0
			},
			legend: {
				show: true,
				position: 'top',
				horizontalAlign: 'right',
				inverseOrder: true,
				markers: {
					width: 30,
					height: 1,
				},
			},
			yaxis: {
				labels: {
					show: true,
					formatter: (value) => { return parseFloat(parseInt(value) / 1000).toString() + "k" },
				}
			},
			tooltip: {
				shared: true,
				intersect: false,
				y: {
					formatter: function (y) {
						if (typeof y !== "undefined") {
							return y.toFixed(0) + " points";
						}
						return y;
					}
				}
			}
		}
		return (
			<div>
				<ReactApexChart options={chartOptions} series={series} type="line" height={250} />
			</div>
		)
	}

	/**
	* @function
	* this function is used to view energy area chart.
	*/
	returnEnergyChart = () => {
		let series = [{
			name: 'This Week',
			type: 'area',
			data: [4400, 5500, 3100, 4700, 3100, 4000, 3900]
		}, {
			name: 'Last Week',
			type: 'line',
			data: [3300, 6900, 3000, 6100, 3300, 3800, 3100]
		}];
		let chartOptions = {
			chart: {
				height: 250,
				type: 'line',
				stacked: false,
				fontFamily: "SourceSansPro-Regular",
				toolbar: {
					show: false,
				},
				zoom: {
					enabled: false,
				}
			},
			title: {
				text: "Energy Consumption (kWh)",
				align: 'left',
				floating: true,
			},
			stroke: {
				curve: 'smooth',
				width: [2, 1],
				dashArray: [0, 8]
			},
			fill: {
				colors: ['#CFEEF7', '#CFEEF7'],
				opacity: 0.9,
				type: 'solid',
				gradient: {
					shade: 'dark',
					type: "horizontal",
					shadeIntensity: 0.5,
					gradientToColors: undefined,
					inverseColors: true,
					opacityFrom: 1,
					opacityTo: 1,
					stops: [0, 50, 100],
					colorStops: []
				},
			},
			colors: ["#5FC8E6", "#cccccc"],
			labels: ["23/6", "24/6", "25/6", "26/6", "27/6", "28/6", "29/6"],
			markers: {
				size: 0
			},
			legend: {
				show: true,
				position: 'top',
				horizontalAlign: 'right',
				inverseOrder: true,
				markers: {
					width: 30,
					height: 1
				},
			},
			yaxis: {
				labels: {
					show: true,
					formatter: (value) => { return parseFloat(parseInt(value) / 1000).toString() + "k" },
				}
			},
			tooltip: {
				shared: true,
				intersect: false,
				y: {
					formatter: function (y) {
						if (typeof y !== "undefined") {
							return y.toFixed(0) + " points";
						}
						return y;
					}
				}
			}
		}
		return (
			<div>
				<ReactApexChart options={chartOptions} series={series} type="line" height={250} />
			</div>
		)
	}

	/** rendering Dashboard information. */
	render() {
		const { classes, popularProducts, dashboardData, pendingOrders } = this.props;

		return (
			<div className={classes.root}>
				<div style={{ display: "flex", justifyContent: "flex-end" }}>
					<div style={{ display: "flex" }}>
						<TextField
							style={{ margin: 5 }}
							variant="filled"
							value={this.state.val}
							InputProps={{ classes: { input: classes.inputDrawerStyle, underline: classes.underline, root: classes.inputDrawerRoot } }}
							select
							onChange={event => this.setState({ val: event.target.value })}
							SelectProps={{
								native: true,
							}}
						>
							<option value="7" style={{ fontFamily: "SourceSansPro-Regular" }}>
								Last 7 days Orders (23 Jun - 30 Jun)
							</option>
							<option value="15" style={{ fontFamily: "SourceSansPro-Regular" }}>
								Last 30 days Orders (23 Jun - 30 Jun)
							</option>
							<option value="25" style={{ fontFamily: "SourceSansPro-Regular" }}>
								Custom date
							</option>
						</TextField>
					</div>
				</div>
				<Grid container spacing={2}>
					<Grid item xs={12} sm={6} md={3}>
						{dashboardData.length > 0 && <Card className={classes.carda}>
							<div className={classes.row}>
								<img src={money} alt="money" size={25} />
								<Typography className={classes.cardBlackTextTitle}>Total Sales</Typography>
							</div>
							<div className={classes.row}>
								<Typography className={classes.cardBlackSecondText}>S$ {dashboardData[0].totalSales}</Typography>
								<Typography className={classes.cardBlackThirdText}>+ 3.55%</Typography>
							</div>
						</Card>}
					</Grid>
					<Grid item xs={12} sm={6} md={3}>
						{dashboardData.length > 0 && <Card className={classes.cardb}>
							<div className={classes.row}>
								<img src={delivery} alt="delivery" size={25} />
								<Typography className={classes.cardWhiteTextTitle}>Total Orders</Typography>
							</div>
							<div className={classes.row}>
								<Typography className={classes.cardWhiteSecondText}>{dashboardData[0].totalOrders}</Typography>
								<Typography className={classes.cardWhiteThirdText}>+ 3.55%</Typography>
							</div>
						</Card>}
					</Grid>
					<Grid item xs={12} sm={6} md={3}>
						{dashboardData.length > 0 && <Card className={classes.cardc}>
							<div className={classes.row}>
								<img src={lighting} alt="lighting" size={25} />
								<Typography className={classes.cardWhiteTextTitle}>Total Energy Consumed</Typography>
							</div>
							<div className={classes.row}>
								<Typography className={classes.cardWhiteSecondText}>{dashboardData[0].totalEnergyConsumed}</Typography>
								<Typography className={classes.cardWhiteThirdText}>+ 3.55%</Typography>
							</div>
						</Card>}
					</Grid>
					<Grid item xs={12} sm={6} md={3}>
						{dashboardData.length > 0 && <Card className={classes.cardd}>
							<div className={classes.row}>
								<img src={users} alt="users" size={25} />
								<Typography className={classes.cardBlackTextTitle}>New Customers</Typography>
							</div>
							<div className={classes.row}>
								<Typography className={classes.cardBlackSecondText}>{dashboardData[0].newCustomer}</Typography>
								<Typography className={classes.cardBlackThirdText}>+ 3.55%</Typography>
							</div>
						</Card>}
					</Grid>
					<Grid item xs={12} sm={12} md={6}>
						<Card 
						 className={classes.whiteCard}
						//    style={{
						// 	position: "absolute",
						// 	width: "561px",
						// 	height: "323px",
						// 	top: "292px",
						// 	backgroundColor:"#FFFFFF",
				        //     boxShadow:"0px 8px 36px rgba(50, 50, 50, 0.08)",
						// 	borderRadius:"8px"
							// box-shadow: 0px 8px 36px rgba(50, 50, 50, 0.08);
							// border-radius: 8px;
						//    }}
						>
							{this.returnSalesChart()}
						</Card>
					</Grid>
					<Grid item xs={12} sm={12} md={6}>
						<Card className={classes.whiteCard}>
							{this.returnEnergyChart()}
						</Card>
					</Grid>
					<Grid item xs={12} sm={12} md={4}>
						<Card className={classes.whiteCard}>
							<div style={{ width: "100%" }}>
								<Typography className={classes.cardTitle}>Action Required</Typography>
								<div className={classes.rowContainer}>
									<Typography className={classes.normalText}>New Orders</Typography>
									<Typography className={classes.textBadge}>10</Typography>
								</div>
								<div className={classes.rowContainer}>
									<Typography className={classes.normalText}>New Orders</Typography>
									<Typography className={classes.textBadge}>10</Typography>
								</div>
								<div className={classes.rowContainer}>
									<Typography className={classes.normalText}>New Orders</Typography>
									<Typography className={classes.textBadge}>10</Typography>
								</div>
								<div className={classes.rowContainer}>
									<Typography className={classes.normalText}>New Orders</Typography>
									<Typography className={classes.textBadge}>10</Typography>
								</div>
							</div>
						</Card>
					</Grid>
					<Grid item xs={12} sm={12} md={8}>
						<Card className={classes.whiteCard}>
							<div style={{ width: "100%", }}>
								<div className={classes.row} style={{ justifyContent: "space-between" }}>
									<Typography className={classes.cardTitle}>Pending Orders</Typography>
									<Button variant="outlined" className={classes.button}>View All</Button>
								</div>
								{pendingOrders.map((item, index) =>
									<div key={index.toString()} className={classes.rowContainer}>
										<Typography className={classes.normalText} style={{ width: "40%" }}>{item.orderNo}</Typography>
										<Typography className={classes.normalText} style={{ width: "30%" }}>{item.orderDate}</Typography>
										<Typography className={classes.normalText} style={{ width: "30%" }}>{item.orderPrice}</Typography>
									</div>)}
							</div>
						</Card>
					</Grid>
					<Grid item xs={12} sm={12} md={8}>
						<Card className={classes.whiteCard}>
							<div style={{ width: "100%" }}>
								<Typography className={classes.cardTitle}>Top Selling Products</Typography>
								{popularProducts.map((product, index) =>
									<div key={index.toString()} className={classes.rowContainer}>
										<Typography className={classes.normalText} style={{ width: "50%", display: 'flex', alignItems: 'center' }}> <Skeleton variant="rect" animation={false} style={{ backgroundColor: "#EEEEED", width: 75, height: 40, marginRight: 10 }} /> {product.productName}</Typography>
										<Typography className={classes.normalText} style={{ width: "25%" }}>{product.units}</Typography>
										<Typography className={classes.normalText} style={{ width: "25%", textAlign: "right" }}>{product.sellingPrice}</Typography>
									</div>
								)}
							</div>
						</Card>
					</Grid>
				</Grid>
			</div>
		);
	}
}

/** Get data from store and assign to props. */
const mapStateToProps = (state) => {
	return {
		dashboardData: state.dashboard_reducers.dashboardData,
		popularProducts: state.dashboard_reducers.popularProducts,
		pendingOrders: state.dashboard_reducers.pendingOrders
	}
};

export default connect(
	mapStateToProps
)(withStyles(styles)(Dashboard));
