import { DASHBOARD_DATA, DASHBOARD_POPULAR_PRODUCTS, DASHBOARD_PENDING_ORDERS } from '../types';
import API from '../../api';

export function getDasboardData() {
    return async dispatch => {
        API.get(`/Maindashboard/GetMaindashboardOverviewCount`).then(async (response) => {
            if (response.isSuccess === true) {
                dispatch({ type: DASHBOARD_DATA, data: response.payload })
            }
        });
    }
}

export function getTopSellingProducts() {
    return async dispatch => {
        API.get(`/Product/GetPopularProducts`).then(async (response) => {
            if (response.isSuccess === true) {
                dispatch({ type: DASHBOARD_POPULAR_PRODUCTS, data: response.payload })
            }
        });
    }
}


export function getPendingOrders() {
    return async dispatch => {
        API.get(`/Maindashboard/GetMaindashboardPendingOrders`).then(async (response) => {
            if (response.isSuccess === true) {
                dispatch({ type: DASHBOARD_PENDING_ORDERS, data: response.payload })
            }
        });
    }
}
